body {
    color: #333;
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 14px;
    line-height: 1.4;
    margin: 0;
    padding: 0
}

a {
    color: #007eff;
    text-decoration: none
}

a:hover {
    text-decoration: underline
}

.container {
    margin-left: auto;
    margin-right: auto;
    max-width: 400px;
    padding: 0 30px
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
    color: #000;
    font-weight: 500;
    line-height: 1;
    margin-bottom: .66em;
    margin-top: 0
}

.h1,
h1 {
    font-size: 3em
}

.h2,
h2 {
    font-size: 2em;
    font-weight: 300
}

.h3,
h3 {
    font-size: 1.25em
}

.h4,
h4 {
    font-size: 1em
}

.h5,
h5 {
    font-size: .85em
}

.h6,
h6 {
